@import url(https://fonts.googleapis.com/css2?family=Roboto&family=Source+Serif+4:wght@400;900&display=swap);
*{
  padding: 0;
  margin: 0;
  font-family: 'roboto', sans-serif;
  font-weight: 400;
}
:root{
  --main-background: rgb(41,41,41);
  --container-background: rgb(19,19,19);
  ---title-color: rgb(226, 226, 226);
  --sec-title-color: rgb(41, 41, 41);
  --product-title-color: rgb(241, 241, 241);
  --text-color: #FFFFFF99;
}
body{
  background-color: rgb(41,41,41);
  background-color: var(--main-background);
  width: 100%;
  height: 100vh;
}
.seperator{
  width: 100%;
  height: 1.5px;
  border-radius: 1rem;
  background-color: #797979;
}
/* app */
.wrapper{
  display: flex;
  width: 94%;
  margin: 3%;
}
.wrapper .left-container{
  width: 20%;
  margin-right: 3%;
  display: flex;
  justify-content: center;
  position: relative;
  
}

.wrapper .right-container{
  width: 77%;
}
.right-container .company-title{
  font-size: 2.25rem;
  font-weight: 700 !important;
  color: var(---title-color);
}
.right-container .sec-title{
  margin-top: 2.5rem;
  font-style: normal;
  font-weight: 500;
  font-size: 1.5rem;
  color: rgba(255, 255, 255, 0.5);
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.slick-next {
  right: -2.5rem !important;
  /* font-size: 2.5rem !important; */
}
.slick-prev{
  left: -2.5rem !important;
}




/* loading and error */
.loading-error-box{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  font-weight: bold;
  color: #FFFFFF;
}

@media screen and (max-width: 680px) {
  .wrapper{
    width: 90%;
    margin: 5%;
    flex-direction: column;
  }
  .wrapper .left-container{
    width: 100%;
    margin: 0 0 2rem 0;
  }
  .wrapper .right-container{
    width: 98%;
  }
  .slick-next {
    right: -2rem !important;
    /* font-size: 2.5rem !important; */
  }
  .slick-prev{
    left: -2rem !important;
  }
  
}
.single-container{
    padding: 0.5rem;
    /* margin: 0 1rem; */
    width: 85%;
    margin-right: 15%;
    /* margin-right: 1rem; */
    background-color:#232323;
    border-radius: 0.5rem;
    color: var(--text-color);
    transition: all 300ms ease;
    position: relative;
  }
  .single-container .single-upper-container{
    display: flex;
    width: 100%;
  }
  .single-upper-container .single-left{
      width: 50%;
  }
  .single-upper-container .single-left .single-img{
    width: 4.75rem;
    height: 4.75rem;
  }
  .single-img img{
    width: 100%;
    height: 100%;
    border-radius: 0.5rem;
  }
  .single-upper-container .single-right{
    /* padding-left: 1.25rem; */
    width: 50%;
  }
  .single-right .info{
    font-size: 0.85rem;
    padding-bottom: 0.5rem;
    font-weight: normal;
  }
  .single-right .name{
    font-size: 1rem;
    color: #ffffff;
  }
  .single-right .price{
    color: #ffffff;
    font-size: 1rem;
  }
  .single-strip{
    display: flex;
    width: 100%;
    margin: 0.5rem 0;
  }
  .location{
    width: 50%;
    font-size: 0.85rem;
  }
  .date{
    font-size: 0.85rem;
  }
  .single-container .description{
    margin-top: 1rem;
    font-size: 0.85rem;
    white-space: pre-wrap;
  }
.product-wrapper{
    margin: 1.5rem 0 2.5rem 0;
  }
  .product-wrapper .product-title{
    font-size: 1.25rem;
    color: var(---title-color);
  }
  .product-wrapper .seperator{
    margin-top: 0.75rem;
  }
  .product-wrapper .product-container{
    margin-top: 1rem;
    padding: 1rem;
    border-radius: 1rem;
    background-color: var(--container-background);
    /* display: flex; */
  }
.filter-container{
    padding: 1rem 2rem;
    width: 100%;
    background-color: var(--container-background);
    border-radius: 1rem;
    height: -webkit-max-content;
    height: max-content;
  }
  .filter-container .seperator{
    margin-top: 0.3rem;
  
  }
  .filter-container .filter-title{
    font-size: 1.5rem;
    color: #A5A5A5;
  }
  .filter-container .filter-options{
    padding-top: 2rem;
  }
  .filter-options .option-container .inner-wrap{
    padding: 0.5rem;
    background-color: #232323;
    color: #ffffff;
    border-radius: 0.3rem;
    margin-bottom: 0.75rem;
    position: relative;
  }
  .option-container .icon{
    position: absolute;
    right: 1rem;
    cursor: pointer;
    transition: all 300ms ease;
  }
  .option-container .icon:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
  }
  .option-container .inner-menu{
    list-style: none;
    margin-bottom: 0.5rem;
    padding-right: 1.5rem;
    display: none;
  }
  .inner-menu.active{
    display: block;
  }
  .inner-menu li{
    color: #FFFFFF;
    background-color: #232323;
    border-radius: 0.3rem;
    padding: 0.5rem;
    margin-bottom: 0.5rem;
    cursor: pointer;
    position: relative;
  }
  .inner-menu li.active{
    background-color: rgb(0, 66, 92);
  }
